import {
  useEffect, useState, useCallback, useRef, useMemo,
  forwardRef,
  createElement as h
} from 'react'


const ChapterTitle = ({ title, titleStyle }) => {
  return h('h4', {
    style: titleStyle
  }, title)
}


const Paragraph = ({ paragraph, style }) => {
  return h('div', { style }, paragraph.content)
}

const Novel = ({ chapter, pages, marginTop, viewportHeight, appStyle, titleStyle, paragraphStyle }) => {
  const [ index, setIndex ] = useState(0)
  const ref = useRef(), prevRef = useRef(), nextRef = useRef(), root = useRef()
  const animateStatus = useMemo(() => {
    return { playing: false }
  }, [])

  const
    prev = pages[index - 1],
    next = pages[index + 1],
    page = pages[index]

  const onClick = useCallback(({ clientX, clientY }) => {
    if(animateStatus.playing || clientY > 100) return
    const newIndex = Math.max(0,
      Math.min(pages.length - 1,
        index + (clientX > 187 ? 1 : -1)
      )
    )
    if(index > newIndex){
      animateStatus.playing = true
      const am = prevRef.current.animate(
        { transform: [ 'skewY(-90deg) scaleX(0)', 'skewY(0deg) scaleX(1)' ] }
      , { duration: 500,
        easing: 'ease-out'
      })
      
      am.onfinish = e => {
        animateStatus.playing = false
        setIndex(newIndex)
      }
    }
    else if(index < newIndex){
      animateStatus.playing = true
      const am = ref.current.animate(
        { transform: [ 'skewY(0deg) scaleX(1)', 'skewY(-90deg) scaleX(0)' ] }
      , { duration: 500,
        easing: 'ease-in'
      })
      
      am.onfinish = e => {
        animateStatus.playing = false
        setIndex(newIndex)
      }
    }
    else{
      alert('first or last')
    }
  }, [ index ])


  useEffect(() => {
    const Hammer = require('hammerjs')
    const hammer = new Hammer(root.current)
    const pageWidth = window.innerWidth
    let c = 0, arrow = 0, pageIndex = index
    const panstart = e => {
      hammer.on('panmove', panmove)
      hammer.on('panend', panend)
    }
    const panmove = ({ deltaX }) => {
      c = deltaX / pageWidth
      if(arrow === 0){
        arrow = c > 0 ? 1 : -1
      }
      switch(arrow){
        case -1:
          c = Math.max(-1, Math.min(0, c))
          if(nextRef.current){
            ref.current.style.transform = `skewY(${ 90 * c }deg) scaleX(${1 + c})`
          }
          break
        case 1:
          c = Math.min(1, Math.max(0, c))
          if(prevRef.current){
            prevRef.current.style.transform = `skewY(${ - 90 * (1 - c) }deg) scaleX(${c})`
          }
          break
      }
    }
    const panend = e => {
      let newIndex = Math.max(0,
        Math.min(pages.length - 1,
          pageIndex - arrow
        )
      )
      switch(arrow){
        case -1:
          if(nextRef.current){
            ref.current.animate(
              { transform: [ `skewY(${ 90 * c }deg) scaleX(${1 + c})`, 'skewY(-90deg) scaleX(0)' ] },
              { duration: 500 * (1 + c), easing: 'ease-out' }
            ).onfinish = () => {
              pageIndex = newIndex
              ref.current.style.transform = `skewY(0deg) scaleX(1)`
              setIndex(newIndex)
            }
          }
          break
        case 1:
          if(prevRef.current){
            prevRef.current.animate(
              { transform: [ `skewY(${ - 90 * (1 - c) }deg) scaleX(${c})`, 'skewY(0deg) scaleX(1)' ] },
              { duration: 500 * (1 - c), easing: 'ease-out' }
            ).onfinish = () => {
              pageIndex = newIndex
              prevRef.current.style.transform = `skewY(-90deg) scaleX(0)`
              setIndex(newIndex)
            }
          }
          break
      }
      arrow = 0
      hammer.off('panmove', panmove)
      hammer.off('panend', panend)
    }
    hammer.on('panstart', panstart)
    return () => {
      hammer.off('panstart', panstart)
    }
  }, [ pages ])
  const pageProps = {
    chapter,
    marginTop, viewportHeight, appStyle, titleStyle, paragraphStyle
  }
  return h('div', {
    ref: root,
    onClick,
    style: {
      position: 'relative',
      width: '100vw',
      height: '100vh',
      overflow: 'hidden'
    }
  },
    h(Page, { ref: nextRef, transform: '', page: next, ...pageProps }),
    h(Page, { ref, transform: `scaleX(1) skewY(0deg)`, page, ...pageProps }),
    h(Page, { ref: prevRef, transform: `scaleX(0) skewY(-90deg)`, page: prev, ...pageProps }),
    )
}

const Page = forwardRef(({ chapter, transform, page, isPrev, isNext, marginTop, viewportHeight, appStyle, titleStyle, paragraphStyle }, ref) => {
  if(!page) return null

  const style = {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100vw',
    height: '100vh',
    paddingTop: marginTop,
    overflow: 'hidden',
    willChange: 'transform',
    background: '#ebe1c6',
    transformOrigin: 'left top',
    border: 'solid 1px #000',
    boxSizing: 'border-box',
    transform
  }
  return <div ref={ref} style={style}>
    <div style={ { height: viewportHeight, overflow: 'hidden' } }>
      <div style={ { transform: `translate(0, ${ page.offset }px)` } }>
        {
          page.paragraphs.map(p => p.type === 'title'
            ? <ChapterTitle key={p.data.key} title={chapter.title} titleStyle={titleStyle}/>
            : <Paragraph key={p.data.key} paragraph={p.data} style={paragraphStyle}/>
          )
        }
      </div>
    </div>
  </div>
})

const Prerender = ({ chapter, appStyle, titleStyle, paragraphStyle, viewportHeight, setPages }) => {
  const ref = useRef()
  useEffect(() => {
    if(viewportHeight){
      const wrapper = ref.current, titleHeight = wrapper.firstChild.getBoundingClientRect().height
      const pages = [
        {
          paragraphs: [ { type: 'title', data: { key: 'title_' + Math.random(), content: chapter.title } } ],
          height: titleHeight,
          offset: 0
        }
      ]
      const { length } = wrapper.children
      let index = 1, page = pages[0]
      while(index < length){
        const pHeight = wrapper.children[index].getBoundingClientRect().height
        const para = {
          type: 'paragraph',
          data: chapter.paragraphs[index - 1]
        }
        if(page.height + page.offset < viewportHeight){
          page.height += pHeight
          page.paragraphs.push(para)
          
          if(page.height + page.offset > viewportHeight){
            page = {
              paragraphs: [ para ],
              height: pHeight,
              offset: page.height + page.offset - viewportHeight - pHeight
            }
            pages.push(page)
          }
        }
        else{
          page = {
            paragraphs: [ para ],
            height: pHeight,
            offset: 0
          }
          pages.push(page)
        }
        index ++
      }
      setPages(pages)
    }
  }, [ chapter, viewportHeight ])
  return h('div', { ref, style: { position: 'absolute', width: '100vw', transform: 'translateX(-200vw)' } },
    h(ChapterTitle, { title: chapter.title, titleStyle }),
    chapter.paragraphs.map(paragraph => h(Paragraph, { key: paragraph.key, paragraph, style: paragraphStyle }))
  )
}

const appStyle = {
  width: '100vw',
  height: '100vh',
  fontSize: 18,
  lineHeight: '32px',
}

const NovelViewer = ({ chapter }) => {
  const [ pageHeight, setPageHeight ] = useState(0)
  const { titleStyle, paragraphStyle, marginTop, viewportHeight } = useMemo(() => {
    const lineHeight = parseFloat(appStyle.lineHeight)
    const viewportHeight = Math.floor(pageHeight - lineHeight * 2 - pageHeight % lineHeight)
    const marginTop = Math.floor((pageHeight - viewportHeight) * .5)

    const titleStyle = {
      fontSize: appStyle.fontSize * 2,
      margin: 0,
      lineHeight: lineHeight * 2 + 'px',
      padding: `0 ${appStyle.fontSize}px ${ lineHeight }px`
    }
    const paragraphStyle = {
      padding: `0 ${appStyle.fontSize}px`,
      boxSizing: 'border-box'
    }

    return { titleStyle, paragraphStyle, marginTop, viewportHeight }
  }, [ pageHeight ])
  useEffect(() => {
    setPageHeight(window.innerHeight)
  }, [ pageHeight ])

  const [ pages, setPages ] = useState(null)
  const viewer = useRef()

  return <>
    <style global jsx>{`
      html, body {
        margin: 0;
        padding: 0;
      }
    `}</style>
    <div ref={viewer} style={appStyle}>
      <Prerender { ...{ chapter, titleStyle, viewportHeight, setPages, appStyle, paragraphStyle } }/>
    {
      pages ? <Novel { ... {
        chapter,
        pages,
        appStyle,
        titleStyle,
        paragraphStyle,
        marginTop,
        viewportHeight
      }}/> : null
    }</div>
  </>
}

